html, body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.static-area {
    position: relative;
    width: 100%;
    height: 100%;
    background: url(../../static/media/poster-about.367c60a2.jpg) no-repeat center;
    background-size: cover;
}

.dynamic-area1   {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: url(../../static/media/poster-drop-animate2.ed0abcfa.png) repeat-x 0px 0px;
    background-size: cover;
    -webkit-animation: posterDrop1 6000s linear infinite;
            animation: posterDrop1 6000s linear infinite;
}

@-webkit-keyframes posterDrop1 {
    from { background-position: 0 0; }
    to { background-position: 4000% 0; }
}

@keyframes posterDrop1 {
    from { background-position: 0 0; }
    to { background-position: 4000% 0; }
}

.dynamic-area2   {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: url(../../static/media/poster-drop-animate1.08f47634.png) repeat-x 0px 0px;
    background-size: cover;
    -webkit-animation: posterDrop2 8000s linear infinite;
            animation: posterDrop2 8000s linear infinite;
}

@-webkit-keyframes posterDrop2 {
    from { background-position: 0 0; }
    to { background-position: 30000% 0; }
}

@keyframes posterDrop2 {
    from { background-position: 0 0; }
    to { background-position: 30000% 0; }
}

body, html { font-size: 100%; 	padding: 0; margin: 0;}

/* Reset */
*,
*:after,
*:before {
	box-sizing: border-box;
}

/* Clearfix hack by Nicolas Gallagher: http://nicolasgallagher.com/micro-clearfix-hack/ */
.clearfix:before,
.clearfix:after {
	content: " ";
	display: table;
}

.clearfix:after {
	clear: both;
}

