* {
    margin: 0;
    padding: 0;
}

html {
    width: 100%;
}

body {
    width: 100%;
}

#container {
    width: 100%;
    height: 1000px;
    left: 70%;
    position: relative;
}

#fireworks {
    position: absolute;
    left: 50%;
    bottom: 30%;
    opacity: 0;
    -webkit-animation: fireworks 4s 2s;
            animation: fireworks 4s 2s;
}

#fireworks img {
    opacity: 0.2;
    border-radius: 400px;
}


@-webkit-keyframes fireworks {
    0%{
        opacity: 0;
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    50%{
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}


@keyframes fireworks {
    0%{
        opacity: 0;
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    50%{
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}

#firecracker {
    position: absolute;
    left: 90%;
    bottom: 0;
    margin-left: -4px;
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-animation: firecracker 3s forwards;
            animation: firecracker 3s forwards;
}

@-webkit-keyframes firecracker {
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
        bottom: 0;
    }
    100%{
        bottom: 50%;
        opacity: 0;
    }
}

@keyframes firecracker {
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
        bottom: 0;
    }
    100%{
        bottom: 50%;
        opacity: 0;
    }
}

#prompt {
    width: 300px;
    height: 100px;
    top: 500px;
    border: 1px solid #000;
    padding: 10px 5px;
    position: absolute;
    -webkit-animation: prompt 3s 0s;
            animation: prompt 3s 0s;
    border-radius: 5px;
}

@-webkit-keyframes prompt {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    80% {
         -webkit-transform: scale(1);
                 transform: scale(1);
    }
    100% {
       opacity: 1;
    }
}

@keyframes prompt {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    80% {
         -webkit-transform: scale(1);
                 transform: scale(1);
    }
    100% {
       opacity: 1;
    }
}

.title {
    font-weight: bold;
    text-align: center;
}

.Button {
    margin: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    text-align: center;
}

.Button button{
    padding: 5px 10px;
    text-align: center;
}


#playChoose {
    left: 70%;
    top: 20px;
    position: absolute;
}

#playChoose button {
    cursor: pointer;
    border: 0;
}

#playChoose a {
    cursor: pointer;
    display: block;
    margin-top: 5px;
}

* {
    padding: 0;
    margin: 0;
}

#map {
    top: 0;
    left: 30%;
    position: absolute;
    margin: 20px auto;
}

.cell {
    width: 50px;
    height: 50px;
    float: left;
    opacity: 0;
    background-color: #fff;
}

.wall {
    width: 50px;
    height: 50px;
    float: left;
    background-image: url(data:image/png;base64,);
}

.target {
    width: 50px;
    height: 50px;
    float: left;
    background-image: url(data:image/jpeg;base64,);
    background-size: cover;
}

.box {
    width: 50px;
    height: 50px;
    position: absolute;
    background-image: url(data:image/png;base64,);
}

.tortoise {
    width: 50px;
    height: 50px;
    position: absolute;
    background-image: url(../../static/media/tortoise.496d3ae0.png);
}

#home {
    width: 80%;
    top: 0;
    left: 12%;
    position: absolute;
    margin: 20px auto;
    text-align: center;
    -webkit-animation: home 3s ease;
            animation: home 3s ease;
}

.container {
    width: 100%;
    height: 100%;
}

#homeButton {
    width: 150px;
    height: 80px;
    top: 420px;
    left: 48%;
    line-height: 80px;
    text-align: center;
    font-weight: bold;
    position: absolute;
    border: 1px solid #000;
    border-radius: 5px;
    -webkit-animation: homeButton 3s ease;
            animation: homeButton 3s ease;
}

@-webkit-keyframes homeButton {
   from {
       -webkit-transform: translateX(500px);
               transform: translateX(500px);
   }
   to {
       -webkit-transform: translateX(0);
               transform: translateX(0);
   }
}

@keyframes homeButton {
   from {
       -webkit-transform: translateX(500px);
               transform: translateX(500px);
   }
   to {
       -webkit-transform: translateX(0);
               transform: translateX(0);
   }
}

@-webkit-keyframes home {
    from {
        -webkit-transform: translateX(-500px);
                transform: translateX(-500px);
    }
    to {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes home {
    from {
        -webkit-transform: translateX(-500px);
                transform: translateX(-500px);
    }
    to {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

#levelChoose {
    margin: 0 auto;
    text-align: center;
    position: relative;
}

#levelChoose a {
    display: block;
    color: #000;
    width: 700px;
    height: 50px;
    font-weight: bold;
    text-decoration: none;
    line-height: 50px;
}

#levelChoose a:hover {
    font-size: 20px;
    border: 1px solid #000;
}

#levelChoose img {
    opacity: 0.5;
}

#imgBox {
    width: 768px;
    height: 1024px;
    margin: 0 auto;
}

.levelLink {
    position: absolute;
    top: 40%;
    left: 30%;
}

